import { isInvalidVal } from '.'

/** cookieÊúâÊïàÊúü-8Â∞èÊó∂ */
const DEFAULT_COOKIE_EXPIRE_TIME = 1000 * 60 * 60 * 8

/**
 * Ê†πÊçÆ‰º†ÂÖ•ÁöÑkeyÂà†Èô§ÂØπÂ∫îÁöÑcookie
 * @param key
 */
export function delCookie(key: string): void {
    setCookie(key, '', -1)
}

/**
 * Ê∏ÖÈô§ÂÖ®ÈÉ®cookie
 */
export function clearCookie(): void {
    const keys = document.cookie.match(/[^ =;]+(?==)/g)
    if (keys) {
        for (let i = 0; i < keys.length; i++) {
            if (keys[i].indexOf('_') === 0) continue
            delCookie(keys[i])
        }
    }
}

/**
 * Ê†πÊçÆ‰º†ÂÖ•ÁöÑkeyËé∑Âèñcookie
 * @param key
 * @returns
 */
export function getCookie(key: string): string {
    const arrStr = document.cookie.split('; ')
    for (let i = arrStr.length - 1; i >= 0; i--) {
        const temp = arrStr[i].split('=')
        if (temp[0].trim() === key) {
            try {
                return decodeURIComponent(temp[1])
            } catch (e) {
                return unescape(temp[1])
            }
        }
    }
    return ''
}

/**
 * Ê∑ªÂä†cookie
 * @param objName
 * @param objValue
 * @param objHours ËøáÊúüÊó∂Èó¥ÔºåÂçï‰Ωçh
 */
export function setCookie(
    key: string,
    val: string,
    timestamp?: number,
    domain?: string
): void {
    const expireTime = isInvalidVal(timestamp)
        ? DEFAULT_COOKIE_EXPIRE_TIME
        : timestamp
    let str = key + '=' + encodeURIComponent(val)
    if (expireTime && expireTime > 0) {
        // ‰∏∫Êó∂‰∏çËÆæÂÆöËøáÊúüÊó∂Èó¥ÔºåÊµèËßàÂô®ÂÖ≥Èó≠Êó∂cookieËá™Âä®Ê∂àÂ§±ÔºåÈªòËÆ§‰∏∫30Â§©
        const date = new Date()
        const ms = expireTime
        date.setTime(date.getTime() + ms)
        str += '; expires=' + date.toUTCString()
    }
    if (domain) {
        str += '; domain=' + domain
    }
    str += '; path=/'
    document.cookie = str
}
